

































































































































































































































































































































































































.ts {
    position: relative;
    left: -60px;
    display: flex;
    max-width: 972px;
    min-width: 700px;
    width: 100%;
    height: 38px;
    line-height: 38px;
    background: #fafafa;
    border-radius: 4px;
    opacity: 0.8;
    border: 1px solid #e8e8e8;
    padding: 0 20px;
    box-sizing: border-box;
    p {
        font-size: 12px;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        color: #666666;
    }
}
.phone_title {
    font-size: 16px;
    font-family: PingFangSC-Medium, PingFang SC;
    font-weight: 500;
    color: #333333;
    line-height: 22px;
}
.content {
    height: 100vh;
}
.phone_row {
    display: flex;
    margin-top: 23px;
}
.phone_box {
    width: 199px;
    height: 80px;

    box-sizing: border-box;
    padding: 10px;
    background: #ffffff;
    box-shadow: 0px 2px 16px 6px rgba(124, 124, 124, 0.1);
    border-radius: 2px;
}
.phone_box > div {
    display: flex;
    justify-content: space-between;
}
.phone_txt {
    font-size: 14px;
    font-family: PingFangSC-Medium, PingFang SC;
    font-weight: 500;
    color: #333333;
    line-height: 20px;
}
.phone_del {
    font-size: 14px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: #fb4702;
    line-height: 20px;
}
.phone_add {
    cursor: pointer;
    margin-left: 20px;
    width: 199px;
    font-size: 14px;
    line-height: 80px;
    text-align: center;
    height: 80px;
    background: #ffffff;
    border-radius: 2px;
    border: 1px dashed #e8e8e8;
}
.img {
    display: flex;
}
.img div {
    margin-right: 23px;
}
.img span {
    font-size: 14px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: #333333;
    line-height: 20px;
}
